const links = window.document.querySelectorAll('a');
const header = document.querySelector('header');
let headerHeight = header.getBoundingClientRect().height;

export const smoothscrollingInit = () =>
  links.forEach((link) => {
    link.addEventListener('click', (e) => {
      const href = link.getAttribute('href');
      headerHeight = header.getBoundingClientRect().height;

      if (href === '#') {
        // Scroll back to top
        e.preventDefault();
        window.scrollTo({ top: 0, behavior: 'smooth' });
      } else if (href.lenght !== 1 && href.startsWith('#')) {
        // Scroll to other internal section
        e.preventDefault();
        const sectionEl = document.querySelector(href);

        // sectionEl.scrollIntoView({ behavior: 'smooth' });

        // console.log(`${sectionEl.offsetTop - headerHeight}`);
        window.scrollTo({
          top: `${sectionEl.offsetTop - headerHeight}`,
          behavior: 'smooth',
        });
      } else {
        // Scroll to others target
        // do normall behavior
      }

      window.setTimeout(() => {
        link.blur();
      }, 1000);
    });
  });
